.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #E6B300;
  min-height: 100vh;
 
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.Title-text {
  color: black;
  font-family: 'Caveat', cursive;
  font-size: 2rem;
}
.Salon-text {
  color: black;
  font-family: 'Caveat', cursive;
  font-size: 2.5rem;
}

.color-back {
  background-color: #E6B300;
}
html {
  scroll-behavior: smooth;
}
.fixed-nav {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1001;
  background-color: #4280AB;
  width: 100%;
  
}

.main-text {
  font-family: 'Graduate', cursive;
  color: black;
}


.addresses{
  color: white;
  border: 1px solid black;
  display: flex;
  justify-content: center;
}


#mapid { 
  height: 460px;
  width: 560px;
 
 }


 .identification {
  display: flex;
  flex-direction: column;
  align-items: center;
  
}
 